.NavBar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  padding: 20px 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  right: 0; 
  z-index: 100; 
}
.NavBarWrapper {
  width: 100%;
  height: 76px;
  background: rgba(72, 34, 168, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  -webkit-transition: all 600ms ease;
  -o-transition: all 600ms ease;
  transition: all 600ms ease;
} 
.Links {
  padding: 20px;
}
@media(min-width: 768px) {
  .NavBar {
    width: 750px;
    margin: 0 auto;
  }
}
@media(min-width: 992px) {
  .NavBar {
    width: 970px;
    padding: 30px 0 10px 0;
  }
}
@media(min-width: 1200px) {
  .NavBar {
    width: 1170px;
  }
}


