.Button {
  font-family: 'Roboto', sans-serif;
  color:  #FFF;
  font-size: 18px;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1px;
  border: none;
  border-radius: 30px;
  background-color:  #4CA9FF;
  -webkit-box-shadow: 0px 0px 0px 0px rgba(76, 169, 255, 0.3);
          box-shadow: 0px 0px 0px 0px rgba(76, 169, 255, 0.3);
  padding: 15px 30px;
  cursor: pointer;
  outline: none;
  -webkit-transition: all 300ms ease;
  -o-transition: all 300ms ease;
  transition: all 300ms ease;
}
@media(min-width: 768px) {
  .Button:hover {
    -webkit-box-shadow: 10px 10px 30px 0px rgba(76, 169, 255, 0.3);
            box-shadow: 10px 10px 30px 0px rgba(76, 169, 255, 0.3);
    background-color: rgb(61, 198, 226);
    color: rgb(72, 34, 168);
  }
}


